import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable, EventEmitter } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { OnboardingInterface, OnboardingUser } from '../interface/OnboardingInterface';
import { downLoadFile } from './global.library';

@Injectable()
export class OnboardingService {
  canExit$: Subject<boolean> = new Subject<boolean>();
  private token: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  private _token = this.token.asObservable();
  private mediaTokenSubject: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  private _mediaTokenSubject = this.mediaTokenSubject.asObservable();
  public templates: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public _templates = this.templates.asObservable();
  public currentTemplate: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public _currentTemplate = this.currentTemplate.asObservable();
  public currentOnboarding: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public _currentOnboarding = this.currentOnboarding.asObservable();
  public onboarding: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public _onboarding = this.onboarding.asObservable();
  public contacts: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public _contacts = this.contacts.asObservable();
  public authentication: EventEmitter<OnboardingUser> = new EventEmitter<OnboardingUser>();
  private header: HttpHeaders;
  public locale: string;
  private endpoint: string;
  private mediaToken: string;
  private user: OnboardingUser;
  public lang = new BehaviorSubject('');
  public _auth: boolean;
  public mainPath: string = ""
  constructor(@Inject('__NgxOnboarding__') config: OnboardingInterface, private http: HttpClient) {
    this.buildHeaders();
    this.initInstance(config);
  }


  private getCookie(name: string) {
    let ca: Array<string> = document.cookie.split(';');
    let caLen: number = ca.length;
    let cookieName = `${name}=`;
    let c: string;

    for (let i: number = 0; i < caLen; i += 1) {
      c = ca[i].replace(/^\s+/g, '');
      if (c.indexOf(cookieName) == 0) {
        return c.substring(cookieName.length, c.length);
      }
    }
  }

  /**
 * @private
 * Generate Header for backend call
 */
  private buildHeaders() {
    this._token.subscribe((token: string) => {
      if (token) {
        const bearer: string = 'Bearer ' + token;
        this.header = new HttpHeaders({
          'Authorization': bearer
        });
      } else {
        const token = this.getCookie('authentication');
        const bearer: string = 'Bearer ' + token;
        this.header = new HttpHeaders({
          'Authorization': bearer
        });
      }

    });
    this._mediaTokenSubject.subscribe((token: string) => {
      if (token) {
        this.mediaToken = token
      } else {
        this.mediaToken = this.getCookie('media-token');
      }

    });
  }


  /**
 * @param config
 * @private
 * Initialize Data for User Instance
 */
  private initInstance(config: OnboardingInterface) {
    this._auth = !!config.auth;
    if (config.endpoint) {
      this.endpoint = config.endpoint;
      this.mediaToken = config.mediaToken;
      if (config.auth) {
        this.authentication.subscribe((user: OnboardingUser) => {
          if (!user.lang && config.lang) {
            user.lang = config.lang;
          }
          this.locale = user.lang;
          this.lang.next(this.locale);
          if (user.token) {
            this.token.next(user.token);
          }
          if (user.mediaToken) {
            this.mediaTokenSubject.next(user.mediaToken);
          }
          this.user = user;
        });
      } else if (config.lang) {
        this.locale = config.lang;
        this.lang.next(this.locale);
        this.user = {
          lang: config.lang,
          token: config.token,
          mediaToken: config.mediaToken
        };
      }
      if (config.token) {
        this.token.next(config.token);
      }
    } else {
      return new Error('Please provide endpoint');
    }
  }

  /**
 * @private
 * Generate Random uniq Id for Konverso Instance
 */
  private guid() {
    let random = () => {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    };
    return random() + random() + '-' + random() + '-' + random() + '-' + random() + '-' + random() + random() + random();
  }



  /**
 * Send Profil To backend
 * @Post Profil
 */
  public async createProfil(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.post(`${this.endpoint}/profils`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        }, () => {
          resolve(false);
        });
    });
  }

  public async updateProfil(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.put(`${this.endpoint}/profils`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        }, () => {
          resolve(false);
        });
    });
  }

  public async deleteProfil(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.delete(`${this.endpoint}/profils/${id}`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }


  public getBuffer(endpoint: string) {
    return this.http.get(endpoint, { responseType: 'arraybuffer' });
  }

  public async getProfil(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/profils/${id}`, options)
        .subscribe((data: any) => {
          this.currentTemplate.next(data)
          resolve(data);
        });
    });
  }

  public async getAllProfil(): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/profils`, options)
        .subscribe((data: any) => {
          this.templates.next(data)
          resolve(data);
        });
    });
  }
  public async getAllTags(): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/tags`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }
  public async getContacts(): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this._contacts.subscribe((contacts) => {
        if (!contacts) {
          this.http.get(`${this.endpoint.split('/onboarding')[0]}/contact/siteList`, options)
            .subscribe((data: any) => {
              this.contacts.next(data.userList)
              resolve(data.userList);
            });
        } else {
          resolve(contacts)
        }
      })
    });
  }




  async asignProfil() {

  }

  async getTags() {

  }


  async getCategory() {

  }


  // CRUD ONBOARDING

  public async createOnboarding(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.post(`${this.endpoint}/onboarding`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async updateOnboarding(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.put(`${this.endpoint}/onboarding`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async deleteOnboarding(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.delete(`${this.endpoint}/onboarding/${id}`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }


  public async getOnboarding(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/onboarding/${id}`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async getAllOnboarding(): Promise<string | any> {
    return await this.http.get(`${this.endpoint}/onboarding`, {
      headers: this.header
    }).subscribe((data: any) => {
      this.onboarding.next(data)
    });
  }


  public async downloadAttachment(url: string, contentType: string, name: string) {
    const path = `${this.endpoint.split('onboarding')[0]}cases/getfile/sf?path=${url}&token=${this.mediaToken}`;
    this.getBuffer(path)
      .subscribe(response => downLoadFile(response, (contentType), name));
  }

  // Create collab

  public async createContact(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.post(`${this.endpoint}/contact`, data, options)
        .subscribe((data: any) => {
          resolve({ data, success: true });
        }, error => resolve({ error, success: false }))
    });
  }
}


