import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'lib-dialog-confirmation',
  templateUrl: './dialog-confirmation.component.html',
  styleUrls: ['./dialog-confirmation.component.scss']
})
export class DialogConfirmation implements OnInit {
  message = ''
  icon = ""
  confirmText = ""
  cancelText = ""
  constructor(@Inject(MAT_DIALOG_DATA) private data: any,
    private dialogRef: MatDialogRef<DialogConfirmation>) {

  }

  ngOnInit() {
    this.message = this.data.message || ''
    this.icon = this.data.icon || 'check-circle'
    this.confirmText = this.data.confirmText || 'Oui'
    this.cancelText = this.data.cancelText || 'Non'
  }
  close(status) {
    this.dialogRef.close(status)
  }

}
